<template>
  <v-row>
    <v-col class="py-0 px-4">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              Пользователи
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['All', 'Admin', 'Moderator', 'User']"
                v-model="group_type"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
      </v-card>
      <v-data-table
        :headers="headers"
        :items="filteredItems"
        :search="search"
      >
        <template v-slot:item.date="props">
          <v-edit-dialog
            :return-value.sync="props.item.date"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.date }}</span>
            <template v-slot:input>
              <v-text-field
                v-model="props.item.date"
                label="Edit"
                single-line
                counter
                autofocus
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.userName="props">
          <v-edit-dialog
            :return-value.sync="props.item.userName"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.userName }}</span>
            <template v-slot:input>
              <v-text-field
                v-model="props.item.userName"
                label="Edit"
                single-line
                counter
                autofocus
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.userRole="props">
          <v-edit-dialog
            :return-value.sync="props.item.userRole"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.userRole }}</span>
            <template v-slot:input>
              <v-card 
                elevation="0" 
                max-width="190"
              >
                <v-select
                  v-model="props.item.userRole"
                  :items="['admin', 'moderator', 'user']"
                  label="Edit"
                  single-line
                  autofocus
                ></v-select>
              </v-card>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.balance="props">
          <v-edit-dialog
            :return-value.sync="props.item.balance"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.balance }}</span>
            <template v-slot:input>
              <v-text-field
                v-model="props.item.balance"
                label="Edit"
                single-line
                counter
                autofocus
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.provider="props">
          <v-edit-dialog
            :return-value.sync="props.item.provider"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.provider }}</span>
            <template v-slot:input>
              <v-card 
                elevation="0" 
                max-width="190"
              >
                <v-select
                  v-model="props.item.provider"
                  :items="['vkontakte', 'local']"
                  label="Edit"
                  single-line
                  autofocus
                ></v-select>
              </v-card>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.deposit="props">
          <v-edit-dialog
            :return-value.sync="props.item.deposit"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.deposit }}</span>
            <template v-slot:input>
              <v-text-field
                v-model="props.item.deposit"
                label="Edit"
                single-line
                counter
                autofocus
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.output="props">
          <v-edit-dialog
            :return-value.sync="props.item.output"
            large
            persistent
            @save="save"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            <span>{{ props.item.output }}</span>
            <template v-slot:input>
              <v-text-field
                v-model="props.item.output"
                label="Edit"
                single-line
                counter
                autofocus
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:item.action="props">
          <v-btn 
            :to="{ name: 'users', params: { id: props.item.userId }}" 
            icon
          >
            <v-icon small>
              mdi-pencil
            </v-icon>
          </v-btn>
        </template> 
      </v-data-table>

      <v-snackbar 
        v-model="snack" 
        :timeout="3000" 
        :color="snackColor"
      >
        {{ snackText }}
        <v-btn 
          text 
          @click="snack = false"
        >
          Close
        </v-btn>
      </v-snackbar>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    snack: false,
    snackColor: '',
    snackText: '',
    group_type: null,
    search: '',
    headers: [
      { text: 'ID', align: 'left', value: 'userId' },
      { text: 'Дата регистрации', value: 'date' },
      { text: 'Имя', value: 'userName' },
      { text: 'Роль', value: 'userRole' },
      { text: 'Баланс', value: 'balance' },
      { text: 'Провайдер', value: 'provider' },
      { text: 'Депозит', value: 'deposit' },
      { text: 'Вывод', value: 'output' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    items: [
      { 
        userId: 1,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Андрей',
        userRole: 'user',
        balance: 150,
        provider: 'vkontakte',
        deposit: 100,
        output: 50
      },
      { 
        userId: 2,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Саша',
        userRole: 'user',
        balance: 250,
        provider: 'vkontakte',
        deposit: 100,
        output: 150
      },
      { 
        userId: 3,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Ваня',
        userRole: 'moderator',
        balance: 1000,
        provider: 'vkontakte',
        deposit: 100,
        output: 800
      },
      { 
        userId: 4,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Катя',
        userRole: 'user',
        balance: 50,
        provider: 'vkontakte',
        deposit: 500,
        output: 100
      },
      { 
        userId: 5,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Артем',
        userRole: 'user',
        balance: 600,
        provider: 'local',
        deposit: 100,
        output: 25
      },
      { 
        userId: 6,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Даша',
        userRole: 'moderator',
        balance: 400,
        provider: 'vkontakte',
        deposit: 100,
        output: 300
      },
      { 
        userId: 7,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Иван',
        userRole: 'admin',
        balance: 450,
        provider: 'vkontakte',
        deposit: 100,
        output: 200
      },
      { 
        userId: 8,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Дима',
        userRole: 'admin',
        balance: 1502,
        provider: 'local',
        deposit: 300,
        output: 350
      },
      { 
        userId: 9,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Артем',
        userRole: 'user',
        balance: 150,
        provider: 'vkontakte',
        deposit: 1100,
        output: 500
      },
      { 
        userId: 10,
        date: new Date().toISOString().substr(0, 10),
        userName: 'Леонид',
        userRole: 'user',
        balance: 1050,
        provider: 'local',
        deposit: 1000,
        output: 50
      }
    ]
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.userRole.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  },
  methods: {
    save() {
      this.snack = true;
      this.snackColor = 'success';
      this.snackText = 'Данные обновлены';
    },
    cancel() {
      this.snack = true;
      this.snackColor = 'error';
      this.snackText = 'Отмена';
    },
    open() {
      this.snack = true;
      this.snackColor = 'info';
      this.snackText = 'Редактирование';
    },
    close() {
      console.log('Dialog closed');
    }
  }
}
</script>
